<template>
    <section class="container">
        <div class="header-area container">
            <a
                class="menu-bars d-black d-sm-block d-md-none d-lg-none d-xl-none"
                role="button"
            >
                <i
                    class="fa fa-bars fa-2x"
                    aria-hidden="true"
                    @click="appearFn()"
                ></i>
            </a>
            <scrollactive :offset="75" :duration="900" :exact="true">
                <div class="logo-side">
                    <a href="#home" target="_self" class="scrollactive-item"
                        >portfo<span>li</span>o</a
                    >
                </div>
            </scrollactive>
            <scrollactive
                class="menu-side d-none d-sm-none d-md-block d-lg-block d-xl-block"
                :offset="75"
                :duration="900"
                :exact="true"
                active-class="active"
            >
                <ul class="list-inline float-right">
                    <li
                        class="list-inline-item"
                        v-for="item in menuItems"
                        :key="item.id"
                    >
                        <a :href="item.hrefId" class="scrollactive-item">
                            <span @click="activeTab = item.id">{{
                                item.name
                            }}</span>
                        </a>
                    </li>
                </ul>
            </scrollactive>
            <scrollactive
                class="menu-active animate__animated"
                :class="appearance"
                :offset="80"
                :duration="900"
                :exact="true"
                active-class="active"
            >
                <ul class="list-group">
                    <li
                        class="list-inline-item"
                        v-for="item in menuItems"
                        :key="item.id"
                    >
                        <a
                            :href="item.hrefId"
                            @click="appearFn()"
                            class="scrollactive-item"
                        >
                            <span>{{ item.name }}</span>
                        </a>
                    </li>
                </ul>
            </scrollactive>
        </div>
    </section>
</template>

<script>
export default {
    name: "Header",
    data: function() {
        return {
            appear: false,
            activeTab: "1",
            animate__bounceInRight: false,
            menuItems: [
                {
                    id: "1",
                    hrefId: "#home",
                    name: "Home"
                },
                {
                    id: "2",
                    hrefId: "#about",
                    name: "About"
                },
                {
                    id: "3",
                    hrefId: "#projects",
                    name: "Projects"
                },
                {
                    id: "4",
                    hrefId: "#resume",
                    name: "Resume"
                }
            ]
        };
    },
    computed: {
        appearance: function() {
            return {
                appear: this.appear,
                animate__bounceInRight: this.animate__bounceInRight
            };
        }
    },
    methods: {
        appearFn() {
            (this.appear = !this.appear),
                (this.animate__bounceInRight = !this.animate__bounceInRight);
        },

        toggleAdvanced: function(event, className) {
            event.target.classList.toggle(className);
        }
    }
};
</script>
